.footer {
    max-width: 1280px;
      margin: 0 auto;
      padding: 60px 0;
      background-color: #fefefe;
      box-shadow: 0px -4px 24px rgba(105, 105, 105, 0.1);
        background-image: url(../images/footer/bg-2@1x_sm.webp),
            url(../images/footer/bg-3@1x_sm-1.webp),
            url(../images/footer/bg-3@1x_sm.webp);

      background-repeat: no-repeat;
      background-position: 0 132px, bottom, top right;
      background-size: 110px 116px, 253px 66px, 104px 259px;
@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
    background-image: url(../images/footer/bg-2@2x_sm.webp),
      url(../images/footer/bg-3@2x_sm-1.webp),
      url(../images/footer/bg-3@2x_sm.webp);
  }

  @media screen and (min-width: 768px) {
    padding: 100px 0;
    background-image: url(../images/footer/bg-1@1x_md.webp),
      url(../images/footer/bg-2@1x_md.webp);
    background-position: bottom, top right;
    background-size: 785px 141px, 203px 365px;

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: url(../images/footer/bg-1@2x_md.webp),
        url(../images/footer/bg-2@2x_md.webp);
    }
  }

  @media screen and (min-width: 1280px) {
    background-image: url(../images/footer/bg@1x_lg.webp);
    background-size: 100%;
    background-position: bottom;

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
      background-image: url(../images/footer/bg@2x_lg.webp);
    }
  }
}
.footer__wrap {

  @media screen and(min-width:1280px) {

    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;
  }
}
.footer__join {
    display: flex;
      gap: 81px;
      margin-bottom: 40px;

  @media screen and(min-width: 768px) {
        gap: 114px;
          align-items: center;
  }
  @media screen and(min-width: 1280px) {
  gap: 145px;
    align-self: flex-start;
    margin-bottom: 0px;
  }
}
.footer_nav {
  display: flex;
    flex-direction: column;
    gap: 20;
    @media screen and (min-width: 768px) {
        flex-direction: row;
        gap: 28;
      }
}
.footer__link {
  font-weight: 400;
    font-size: 14px;
    line-height: calc(16/14);
  
    color: var(--black-color);

    display: block;

      @media screen and (mix-width: 768px) {
        padding: 28px 0;
      }
}
.footer__link:hover, .footer__link:focus { 
  color: var(--accent-txt-color);
  text-decoration: underline;


}

.footer__logo {

  align-self: flex-start;
  /*
  margin-right: 95px;

  @media screen and(min-width:768px) {
    margin-right: 114px;
  }

  @media screen and(min-width:1280px) {
    margin-right: 159px;
  } */
}


.footer__titel {
  display: block;
    margin-bottom: 12px;
    font-weight: 600;
    font-size: 14px;
    line-height: calc(21 / 14);
  margin-bottom: 12px;


  color: #2c2c2c;
}

.footer__form {

  @media screen and (max-width: 767px) {
      margin-bottom: 20px;
    }
  
    @media screen and (min-width: 768px) {
      display: flex;
    }

}
.footer-label__text {
  display: block;
  margin-bottom: 12px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.5;
}

.footer-label__input {
  width: 279px;
  display: inline-block;

  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }

  @media screen and (min-width: 768px) {
    margin-right: 14px;
  }
}

.footer__text {
  font-size: 10px;
  line-height: calc(12 / 10);

  @media screen and (max-width: 767px) {
    margin-bottom: 60px;
  }

  @media screen and (max-width: 1279px) {
    margin-bottom: 100px;
  }

  @media screen and (min-width: 768px) {
    font-size: 12px;
    line-height: calc(14 / 12);
  }
}
.subscribtion-form__input {
  width: 100%;
  height: 42px;
  border-radius: 30px;
  color: var(--black-color);
  padding: 12px 12px;
  border: 0.854167px solid #ffe550;
  outline: transparent;

  &:invalid:not(:placeholder-shown) {
    border: 0.854167px solid #ff705d;
  }

  &:valid:not(:placeholder-shown) {
    border: 0.854167px solid #7eb101;
  }

  @media screen and (min-width: 768px) {
    width: 394px;
  }

  &::placeholder {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    font-weight: 400;
    font-size: 12px;
    line-height: calc(18 / 12);
    color: #c4c4c4;
    letter-spacing: 0.03em;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.subscribtion-form__input:invalid:not(:placeholder-shown)~.subscribtion-form__title {
  opacity: 1;
}

.subscribtion-form__title {
  display: none;
  width: 100%;
  margin-top: 4px;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    opacity: 0;
  }
}

.subscribtion-form__text-error {
  text-align: left;
  font-size: 10px;
  line-height: calc(15 / 10);
  margin-left: 4px;
  margin-top: 4px;
  color: #ff705d;
}

.subscribtion-form__title--footer {
  margin-top: 0;
}

.subscribtion-form__text-error--footer {
  margin-top: 0;
}
.subscribtion-form__btn {
  min-width: 103px;
  padding: 10px 20px;
  background-color: #BBF330;
  border: none;
  transition: background-color var(--anim-hover);

  &:active,
  &:hover,
  &:focus {
    background-color: #BBF330;
  }
}

.footer__btn {
  min-width: 103px;
  font-size: 13px;
  line-height: 1.5;
  padding: 10px 17px;
  background-color: #FFFFFF;
  border: 0.854167px solid #7eb101;
  border-radius: 30px;
  transition: background-color var(--anim-hover);

  &:active,
  &:hover,
  &:focus {
    background-color: var(--bg-color);
  }

  @media screen and (max-width: 767px) {
    display: block;
    margin-left: auto;
  }

  @media screen and (min-width: 768px) {
    align-self: flex-start;
  }
}
.footer__link-doc {
  color: #7eb101;
  font-size: 10px;
  line-height: calc(12 / 10);
  
  

  @media screen and (min-width: 768px) {
    font-size: 12px;
    line-height: calc(14 / 12);
  }}

.footer-info__list {
  
      display: flex;
      gap: 32px;
  
      @media screen and (max-width: 767px) {
        margin-bottom: 40px;
        justify-content: space-between;
      }
  
      @media screen and (min-width: 768px) {
        gap: 20px;
      }
    
}
.footer__description {
  font-size: 10px;
  line-height: calc(12 / 10);

  @media screen and (max-width: 767px) {
    text-align: center;
  }

  @media screen and (min-width: 768px) {
    font-size: 12px;
    line-height: calc(14 / 12);
  }
}
.footer-info__link {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  text-decoration-line: underline;
  color: #7eb101;

  @media screen and(max-width:480px) {
    font-size: 10px;
    line-height: calc(12 / 10);
  }

  @media screen and(min-width:768px) {
    font-size: 12px;
    line-height: calc(14 / 12);
  }
}

.footer-copy__item {
  text-align: center;

  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  color: #2c2c2c;

  @media screen and(max-width:480px) {
    font-size: 10px;
    line-height: calc(10 / 12);
  }

  @media screen and(min-width:768px) {
    font-size: 12px;
    line-height: calc(14 / 12);
    margin-left: auto;
  }
}

.footer__info  {
   @media screen and (min-width: 768px) {
     display: flex;
     align-items: center;
     justify-content: space-between;
   }
 }
